//Variables
$light-pink: #faece6
$light-green: #95d2ba
$light-yellow: #fdd982
$dark-pink: #991e5c
$yellow: #b1985b
$green: #1e2a25


*
  font-size: 62.5%
  margin: 0
  padding: 0
  font-family: 'Caveat', cursive
  overflow-x: hidden
body
  background-color: $light-pink

//Nav Bar
nav.navbar
  min-height: 10vh
  background-color: $light-yellow !important
  border-bottom: white solid .1rem
  width: 100vw
  position: fixed
  
  div.navbar-brand  
    background-color: $light-yellow !important  
    .home-text
      font-size: 3rem
      background-color: $light-yellow !important
      height: 100%
      color: darken($yellow,30%) !important
      padding-left: 2rem
      @media screen and ( min-width: 700px )
        font-size: 3.5rem
  div.collapse
    background-color: $light-yellow !important
  ul.navbar-nav
    background-color: $light-yellow !important
    width: 100%
    justify-content: space-evenly
    li
      background-color: $light-yellow !important
      a
        font-size: 2rem
        color: darken($yellow,30%) !important
        @media screen and ( min-width: 700px )
          font-size: 3rem

span.navbar-toggler-icon
  min-height: 2.5rem
  min-width: 3.5rem
  max-width: 4rem
  max-height: 4rem
  background-color: $yellow
  border-radius: 5rem
  

//Landing Page
div.landing
  padding-top: 10rem
  height: 90vh
  display: flex
  flex-direction: column
  width: 75vw
  align-items: center
  margin: auto
  div.imgbox
    padding-top: 5rem
    max-width: 100rem
    max-height: 65vh
    img
      height: 100%
      width: 100%
      overflow: hidden
    
  div.shadow
    height: 2rem
    max-width: 50rem
    width: 45vw
    background-color: darken($light-pink, 75%)
    border-radius: 100%
  h1.kstudios
    margin-top: 2rem
    font-size: 4rem
    text-align: center
    color: $dark-pink
    overflow: hidden
    @media screen and ( min-width: 700px )
      font-size: 4.5rem
      



//Footer
footer
  background-color: $light-green
  width: 100vw
  // height: 20vh
  min-height: 35rem
  h2, p
    color: $green
  div.footer-box
    max-width: 1024px
    margin:  auto
    height: 100%
    overflow: hidden
    padding: 2rem
    h2
      font-size: 3.5rem
      text-align: center
      overflow: hidden
    p
      font-size: 2rem
      padding: 1rem 0
      @media screen and ( min-width: 700px )
        width: 75%
        margin: auto
        text-align: center
    p.cr
      text-align: center
    
div.footbox
  display: flex
  padding: 2rem 0
  align-items: center
  justify-content: space-around
  width: 100%
  margin: auto
  @media screen and ( min-width: 700px )
    width: 75%
    flex-direction: column
    
  .footer-panda
    img
      max-height: 12rem
      @media screen and ( min-width: 700px )
        max-height: 15rem
  .footer-icons
    display: flex
    justify-content: space-around
    margin: auto
    img
      max-height: 5rem   
      @media screen and ( min-width: 700px )
        max-height: 8rem
